/* ==========================================================================
   Dark Mode Toggle
   ========================================================================== */

	$(document).ready(function() {
		
		var mq = window.matchMedia( '(preferb-color-scheme: dark)' );
		
		if (localStorage.getItem('rovenblogmode') == 'dark-mode') {
			$( 'body' ).addClass( 'rb-dark-mode' );
			$( '.rb-color-scheme-toggler i' ).toggleClass('rovenblog-icon-sun rovenblog-icon-moon');
		} else if (localStorage.getItem('rovenblogmode') == 'light-mode'){
			$( 'body' ).removeClass( 'rb-dark-mode' );
			$( '.rb-color-scheme-toggler i' ).toggleClass('rovenblog-icon-sun rovenblog-icon-moon');
		}
		
		if (localStorage.getItem('rovenblogmode') == 'light-mode'){
			$( 'body' ).removeClass( 'rb-dark-mode' );
			$( '.rb-color-scheme-toggler i' ).toggleClass('rovenblog-icon-sun rovenblog-icon-moon');
		} else if ( mq.matches ){
			$( 'body' ).addClass( 'rb-dark-mode' );
			$( '.rb-color-scheme-toggler i' ).toggleClass('rovenblog-icon-sun rovenblog-icon-moon');
		}
		
		$( '.rb-color-scheme-toggler' ).on('click', function(event) {	
			event.preventDefault();	
			if( $( 'body' ).hasClass( 'rb-dark-mode' )) {
				$( 'body' ).removeClass( 'rb-dark-mode' );
				$( '.rb-color-scheme-toggler i' ).toggleClass('rovenblog-icon-sun rovenblog-icon-moon');
				localStorage.setItem('rovenblogmode','light-mode');
			} else {
				$( 'body' ).addClass( 'rb-dark-mode' );
				$( '.rb-color-scheme-toggler i' ).toggleClass('rovenblog-icon-sun rovenblog-icon-moon');
				localStorage.setItem('rovenblogmode','dark-mode');
			}
		});
		
	});
   
/* ==========================================================================
   Search
   ========================================================================== */   
   
	$(document).ready(function() {
	   
		var searchContainer = $( '#rb-search' );
		var searchField = $( '#rb-search input[type="search"]' );
	   
		$('.rb-search-toggler').on( 'click', function(event) {
			
			event.preventDefault();
			
			searchContainer.toggleClass( 'active' ).slideToggle(300);
			
			if ( searchContainer.hasClass( 'active' ) ) {
				searchField.focus();
			} else {
				searchField.blur();
			}	
			
		});	
		
		$( '#rb-search-close' ).on( 'click', function( event ) {
			
			event.preventDefault();
			searchContainer.toggleClass( 'active' ).slideToggle(300);
			$('.rb-search-toggler').focus();
			
		} );

	});
   
/* ==========================================================================
   Sticky Menu
   ========================================================================== */
   
   // https://github.com/yowainwright/stickybits
   
	if( $('#rb-header').length && ( $('body').hasClass("rb-sticky-header-mobile") || $('body').hasClass("rb-sticky-header-desktop") ) ) {

		$("#rb-header").stickybits({ 
			useStickyClasses: true, 
			stickyClass: "rb-header-sticky",
			stuckClass: "rb-header-sticky",
			noStyles: true
		});

	}

/* ==========================================================================
   Mobile Menu
   ========================================================================== */
   
	var MOBILEBREAKPOINT = 1200;
   
	function handleMobileMenu() {

		if ($(window).width() >= MOBILEBREAKPOINT) {
			$('body').removeClass('rb-mobile-menu-active');
		} 

	}

	function showHideMobileMenu() {
		
		$(".rb-mobile-menu-trigger").on("click", function(event) {
			event.preventDefault();
			$('body').addClass('rb-mobile-menu-active');
			$("#rb-mobile-menu-content-inner > *:first-child a").first().focus();
		});
		
		$("#rb-mobile-menu-close, #rb-site-overlay").on("click", function(event) {
			event.preventDefault();
			$('body').toggleClass('rb-mobile-menu-active');
			$(".rb-mobile-menu-trigger").focus();
		});
		
		$('#rb-mobile-menu li.menu-item-has-children > a').each(function() {
			var $t = $(this);
			$t.append('<span class="rb-mobile-menu-has-submenu rb-closed"><i class="rovenblog-icon-chevron-down"></i></span>');

		});

		$('.rb-mobile-menu-has-submenu').on('click', function(event) {
			var $t = $(this);
			if ($t.hasClass('rb-closed')) {
				$t.removeClass('rb-closed').addClass('rb-opened').parent().siblings('ul').fadeIn();
				$t.find('i').removeClass('rovenblog-icon-chevron-down').addClass('rovenblog-icon-chevron-up');
			} else {
				$t.removeClass('rb-opened').addClass('rb-closed').parent().siblings('ul').fadeOut();
				$t.find('i').removeClass('rovenblog-icon-chevron-up').addClass('rovenblog-icon-chevron-down');
			}
			event.preventDefault();
		});
		
	}

	$(document).ready(function() {
		
		handleMobileMenu();
		showHideMobileMenu(); 
		
	});

	$(window).resize(function() {
			
		handleMobileMenu();

	});

/* ==========================================================================
   Back to top
   ========================================================================== */
   
	function handleBackToTop() {
	   
		$('#rb-back-to-top').on('click', function(event){
			event.preventDefault();
			$('html, body').animate({scrollTop:0}, 'slow');
		});
   
	}

	function showHidebackToTop() {
	
		if ($(window).scrollTop() > $(window).height() / 2 ) {
			$("#rb-back-to-top").removeClass('gone').addClass('visible');
		} else {
			$("#rb-back-to-top").removeClass('visible').addClass('gone');
		}
	
	}

	$(document).ready(function() {

		handleBackToTop();
		showHidebackToTop(); 

	});

	$(window).scroll(function() {				   
		
		showHidebackToTop(); 

	});
	
/* ==========================================================================
   Read Time
   ========================================================================== */
   	
	$(document).on("scroll", function(){
		var pixels = $(document).scrollTop();
		var pageHeight = $('#rb-main-content').height() - $(window).height();
		var progress = 100 * pixels / pageHeight;
		$("#rb-scroll-progress").css("width", progress + "%");
	});

/* ==========================================================================
   Lazy Loading via lozad script
   ========================================================================== */
	
	if ((typeof rovenblog_args !== 'undefined') && (rovenblog_args.lazy_load == true)) {
		if (typeof lozad !== 'undefined') {
			lozad().observe();
		}
	}

/* ==========================================================================
   Ajax call for post count
   ========================================================================== */
	
	if ((typeof rovenblog_args !== 'undefined') && (rovenblog_args.ajax_call == true)) {
		$(function() {
			// Ajax for post views count.
			$.ajax({
				type: 'post',
				url: rovenblog_args.requestURL,
				async: true,
				data: {
					action: 'rovenblog_count_start',
					rb_nonce: rovenblog_args.nonce,
					rb_post_id: rovenblog_args.post_id,
				}
			});

		});
	}
	
/* ==========================================================================
   Contact form validation
   ========================================================================== */	
	
	$(document).ready(function() {

		if ( (typeof rovenblog_args !== 'undefined') && $('#comment-form').length && (typeof $.fn.validate != 'undefined') ) {

			$('#comment-form').validate({
				rules: {
					comment: {
						required: true
					},
					
					author: {
						required: true,
						minlength: 2
					},

					email: {
						required: true,
						email: true
					}
				},
				messages: {
					comment: rovenblog_args.text_comment,
					author: rovenblog_args.text_name,
					email: {
						required: rovenblog_args.text_email,
						email: rovenblog_args.text_email_v
				  }
				},
				errorElement: "div",
				errorPlacement: function(error, element) {
					element.after(error);
				}
			});
			
		}	
	
	});	
	
	
